




















































































































































































































































.fstyle {
  /deep/ div {
    display: flex;
  }
}
.formlist {
  margin-top: 40px;
}
.department {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  .el-dialog {
    width: 40%;
  }
  .department-left {
    width: 30%;

    .left-title {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      font-size: 14px;
      background: #e4e4e4;
      padding: 12px;
      button {
        height: 32px;
      }
      p {
        line-height: 32px;
      }
    }

    .tree {
      border: 1px solid #e4e4e4;
      height: 100%;
      padding: 20px;
      /deep/ .el-tree-node {
        margin-bottom: 4px;
      }
    }
  }
  .department-right {
    width: 66%;
    /deep/ .c1 {
      min-width: 370px;
    }
    .right-title {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      font-size: 14px;
      background: #e4e4e4;
      padding: 12px;
      border: 1px solid #e4e4e4;
      button {
        height: 32px;
      }
      p {
        line-height: 32px;
      }
    }
  }
}
