






























































































































































































.follow-card {
  .follow-header {
    font-size: 18px;
    height: 100%;
    padding: 0 20px;
    div {
      align-items: center;
      display: flex;
      justify-content: space-between;
    }
  }

  .timeline-item,
  .follow-content {
    display: flex;
    justify-content: space-between;
    margin: 20px 0 10px;
    font-size: 14px;
  }
  .reply {
    & > div {
      margin-top: 15px;
      padding-top: 10px;
      border-top: 1px solid #ddd;
    }
    line-height: 24px;
    p {
      font-size: 16px;
      color:#555;
    }
    div.reply-name-time {

      display: flex;
      justify-content: space-between;
    }
    span {
      color: #999;
    }
  }
  .timeline-contents {
    font-size: 18px;
    line-height: 28px;
  }
}
/deep/.el-dialog {
  width: 40%;
  padding: 0 20px;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
.pager {
  text-align: right;
}
