  cursor: pointer;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-left: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
/deep/ .el-card {
  width: 48%;
  margin-bottom: 20px;
}
/deep/ .el-tab-pane {
  position: relative;
}
.genjin {
  position: absolute;
  top: 0;
  right: 0;
}
span {
  font-size: 18px;
}
div.text {
  font-size: 14px;
  margin-bottom: 18px;
  display: flex;
  & > :first-child {
    width: 100px;
    // text-align: right;
    // margin-right:10px;
  }
  & > :last-child {
    flex: 1;
    word-break: break-all;
    word-wrap: break-word;
  }
}
.font14 {
  font-size: 14px;
  line-height: 24px;
  & > div.title {
    font-weight: bold;
    font-size: 16px;
  }
}
.remark {
  padding-right: 20px;
  box-sizing: border-box;
  span {
    // display: line-block;
    word-break: break-all;
    word-wrap: break-word;
    font-size: 14px;
  }
}
