  cursor: pointer;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-left: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.basic {
  font-size: 14px;
  div {
    margin-bottom: 18px;
    display: flex;
    & > :first-child {
      width: 100px;
      & > :last-child {
        flex: 1;
        word-break: break-all;
        word-wrap: break-word;
      }
    }
  }
}

.text {
  font-size: 14px;
  margin-bottom: 18px;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.box-card {
  width: 48%;
  margin-top: 20px;
}
.genjin {
  position: absolute;
  top: -20px;
  right: 0;
}
.quanjing {
  position: relative;
}
span {
  font-size: 18px;
}
