  width: 40%;
  .el-input__inner {
    width: 50%;
  }
}

.list-form {
  padding: 10px 20px;
  .el-radio-group {
    width: 100%;
    font-size: inherit;
  }
  li {
    padding: 20px 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p {
      width: 280px;
    }
  }
}
.remind {
  margin: 20px 0px;
  padding: 20px 8px;
  background: rgba(223, 240, 216, 1);
  color: rgb(119, 166, 122);
}
.user-content {
  padding: 30px 10px 10px 30px;
  border: 1px solid rgb(228, 228, 228);
  height: 100%;
  margin-top: 20px;
  display: flex;
  .user-left {
    width: 30%;
    .el-menu {
      border: 1px solid rgb(228, 228, 228);
      // /deep/ :first-child.el-menu-item{
      //   background: #ddd;
      // }
      /deep/ .el-menu-item {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
      }
    }
    p {
      background: rgb(228, 228, 228);
      padding: 10px;
      font-size: 16px;
    }
  }
  .user-right {
    margin-left: 20px;
    width: 100%;
    .user-center {
      p {
        background: rgb(245, 245, 245);
        padding: 20px 20px;
        font-size: 14px;
        margin-top: 10px;
      }
      div.content {
        padding: 20px;
      }
      .el-tabs__content {
        padding: 0;
      }
      .el-checkbox {
        margin-bottom: 30px;
      }
    }
  }
}
