













































































































































.follow-card {
  .follow-header {
    font-size: 18px;
    height: 100%;
    padding: 0 20px;
    div {
      align-items: center;
      display: flex;
      justify-content: space-between;
    }
  }
  .timeline-item {
    display: flex;
    justify-content: space-between;
    margin: 20px 0 10px;
    font-size: 14px;
  }
  .timeline-contents {
    font-size: 15px;
  }
}
.pager {
  text-align: right;
}
