







































































.information {
  position: relative;
  & > div {
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    width: 400px;
  }
}

.cipher {
  width: 30%;
  margin: auto;
  margin-top: 20%;
}

.el-form {
  .el-form-item {
    /deep/ .el-form-item__label {
      padding-right: 40px;
    }
  }
}
.btn {
  text-align: right;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
/deep/ .el-upload {
  border: 1px solid #ddd;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
