  .el-form-item {
    padding: 0px 80px;
  }
  /deep/ .el-dialog {
    margin-top: 15vh;
    height: 80%;
    // overflow: auto;
  }
  /deep/ .el-dialog__body {
    height: 80%;
  }
}
.el-dropdown-link {
  cursor: pointer;
  // color: #409EFF;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-left: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.item {
  margin-bottom: 18px;
  font-size: 14px;
  display: flex;
  & > p:first-child {
    width: 100px;
  }
  & > p:last-child {
    flex: 1;
    word-break: break-all;
    word-wrap: break-word;
  }
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.box-card {
  width: 48%;
  margin-top: 20px;
}
.genjin {
  position: absolute;
  top: -20px;
  right: 0;
}

.itemPage {
  margin-top: 20px;
  margin-right: 10px;
  float: right;
}
.quanjing {
  position: relative;
}
span {
  font-size: 18px;
}
