  cursor: pointer;
}
.returned-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-right: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.text {
  font-size: 14px;
  line-height: 40px;
  display: flex;
  & > :first-child {
    width: 110px;
  }
  & > p:last-child {
    flex: 1;
    word-break: break-all;
    word-wrap: break-word;
  }
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.box-card {
  width: 48%;
  margin-top: 20px;
}
.genjin {
  position: absolute;
  top: 0;
  right: 0;
  width: 48%;
}
.quanjing {
  position: relative;
}
span {
  font-size: 18px;
}
