  margin-bottom: 20px;
  text-align: center;
}

.el-form {
  .el-form-item {
    margin: 8px 0;
  }
}

.slide {
  transition: all 0.5s;
  overflow: hidden;
}

ul {
  list-style-type: none;
  padding: 0;
}
.list {
  li {
    display: inline-block;
    padding: 0 20px;
    cursor: pointer;
  }
}
.addclass {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}

.item {
  margin-bottom: 18px;
  margin-left: 20px;
}

.item-css {
  width: 100%;
}
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}

.input-search button {
  margin-left: 30px;
}
