  margin-top: 30px;
  margin-right: 10px;
  float: right;
}

/deep/.el-table__row--level-1 {
  background: oldlace;
}

div {
  .ditch-search {
    span {
      display: inline-block;
      font-size: 14px;
      width: 80px;
